ask not found\";\r\n\t\t\t\t}\r\n\t\t\t})\r\n\t\t\t.catch( error => {\r\n\t\t\t\tthis.errMsg = \"Error deleting task\";\r\n\t\t\t});\r\n\t\t}\r\n\t}\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.task{\r\n\tbackground: #4f667d;;\r\n\tpadding: 15px;\r\n}\r\n\r\n.task:nth-of-type(2n+1){\r\n\tbackground: #34495E;\r\n}\r\n\r\n.task-check{\r\n\tcursor: pointer;\r\n}\r\n\r\n.completed{\r\n\ttext-decoration: line-through;\r\n}\r\n\r\n.task-text{\r\n\tdisplay: inline-block;\r\n\twidth: 90%;\r\n\ttext-align: center;\r\n\tcolor: #fff;\r\n}\r\n\r\n.fa-trash{\r\n\tcolor: #ff0000;\r\n\tcursor: pointer;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
