___CSS_LOADER_EXPORT___.push([module.id, ".widget-user-header{background-position:50%;background-size:cover;height:250px!important}.widget-user .card-footer{padding:0}", "",{"version":3,"sources":["webpack://./resources/js/components/Profile.vue"],"names":[],"mappings":"AAEA,oBACA,uBAAA,CACA,qBAAA,CACA,sBACA,CACA,0BACA,SACA","sourcesContent":["\r\n<style>\r\n.widget-user-header{\r\n    background-position: center center;\r\n    background-size: cover;\r\n    height: 250px !important;\r\n}\r\n.widget-user .card-footer{\r\n    padding: 0;\r\n}\r\n</style>\r\n\r\n\r\n<template>\r\n    <div class=\"container\">\r\n        <div class=\"row\">\r\n            <div class=\"col-md-12 mt-3\">\r\n                <div class=\"card card-widget widget-user\">\r\n                <div class=\"widget-user-header text-pink\" style=\"background-image:url('./image/user-cover.jpg')\">\r\n                    <h1 class=\"widget-user-username \">{{this.form.name}}</h1>\r\n                    <h5 class=\"widget-user-desc\">{{this.form.type}}</h5>\r\n                </div>\r\n\r\n                <div class=\"widget-user-image\">\r\n                       <img class=\"img-circle\" :src=\"getProfilePhoto()\" alt=\"User Avatar\">\r\n                </div>\r\n                <div class=\"card-footer\">\r\n                    <div class=\"row\">\r\n                    <div class=\"col-sm-4 border-right\">\r\n                        <div class=\"description-block\">\r\n                        <h5 class=\"description-header\">3,200</h5>\r\n                        <span class=\"description-text\">SALES</span>\r\n                        </div>\r\n                    </div>\r\n                    <div class=\"col-sm-4 border-right\">\r\n                        <div class=\"description-block\">\r\n                        <h5 class=\"description-header\">13,000</h5>\r\n                        <span class=\"description-text\">FOLLOWERS</span>\r\n                        </div>\r\n                    </div>\r\n                    <div class=\"col-sm-4\">\r\n                        <div class=\"description-block\">\r\n                        <h5 class=\"description-header\">35</h5>\r\n                        <span class=\"description-text\">PRODUCTS</span>\r\n                        </div>\r\n                    </div>\r\n                    </div>\r\n                </div>\r\n                </div>\r\n            </div>\r\n\r\n\r\n\r\n            <div class=\"col-md-12\">\r\n                <div class=\"card\">\r\n                    <div class=\"card-header p-2\">\r\n                        <ul class=\"nav nav-pills\">\r\n                        <li class=\"nav-item\"><a class=\"nav-link\" href=\"#activity\" data-toggle=\"tab\">Activity</a></li>\r\n                        <li class=\"nav-item\"><a class=\"nav-link active show\" href=\"#settings\" data-toggle=\"tab\">Settings</a></li>\r\n                        </ul>\r\n                    </div>\r\n                    <div class=\"card-body\">\r\n                        <div class=\"tab-content\">\r\n\r\n                            <div class=\"tab-pane\" id=\"activity\">\r\n                                <h3 class=\"text-center\">Display User Activity   </h3>\r\n                            </div>\r\n\r\n                            <div class=\"tab-pane active show\" id=\"settings\">\r\n                                <form class=\"form-horizontal\">\r\n                                <div class=\"form-group\">\r\n                                    <label for=\"inputName\" class=\"col-sm-2 control-label\">Name</label>\r\n\r\n                                    <div class=\"col-sm-12\">\r\n                                    <input type=\"\" v-model=\"form.name\" class=\"form-control\" id=\"inputName\" placeholder=\"Name\" :class=\"{ 'is-invalid': form.errors.has('name') }\">\r\n                                     <has-error :form=\"form\" field=\"name\"></has-error>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"form-group\">\r\n                                    <label for=\"inputEmail\" class=\"col-sm-2 control-label\">Email</label>\r\n\r\n                                    <div class=\"col-sm-12\">\r\n                                    <input type=\"email\" v-model=\"form.email\" class=\"form-control\" id=\"inputEmail\" placeholder=\"Email\"  :class=\"{ 'is-invalid': form.errors.has('email') }\">\r\n                                     <has-error :form=\"form\" field=\"email\"></has-error>\r\n                                    </div>\r\n                                </div>\r\n\r\n                                <div class=\"form-group\">\r\n                                    <label for=\"inputExperience\" class=\"col-sm-2 control-label\">Experience</label>\r\n\r\n                                    <div class=\"col-sm-12\">\r\n                                    <textarea  v-model=\"form.bio\" class=\"form-control\" id=\"inputExperience\" placeholder=\"Experience\" :class=\"{ 'is-invalid': form.errors.has('bio') }\"></textarea>\r\n                                     <has-error :form=\"form\" field=\"bio\"></has-error>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"form-group\">\r\n                                    <label for=\"photo\" class=\"col-sm-2 control-label\">Profile Photo</label>\r\n                                    <div class=\"col-sm-12\">\r\n                                        <input type=\"file\" @change=\"updateProfile\" name=\"photo\" class=\"form-input\">\r\n                                    </div>\r\n\r\n                                </div>\r\n\r\n                                <div class=\"form-group\">\r\n                                    <label for=\"password\" class=\"col-sm-12 control-label\">Change Password (leave empty if not changing)</label>\r\n\r\n                                    <div class=\"col-sm-12\">\r\n                                    <input type=\"password\"\r\n                                        v-model=\"form.password\"\r\n                                        class=\"form-control\"\r\n                                        id=\"password\"\r\n                                        placeholder=\"Passport\"\r\n                                        :class=\"{ 'is-invalid': form.errors.has('password') }\"\r\n                                    >\r\n                                     <has-error :form=\"form\" field=\"password\"></has-error>\r\n                                    </div>\r\n                                </div>\r\n\r\n                                <div class=\"form-group\">\r\n                                    <div class=\"col-sm-offset-2 col-sm-12\">\r\n                                    <button @click.prevent=\"updateInfo\" type=\"submit\" class=\"btn btn-success\">Update</button>\r\n                                    </div>\r\n                                </div>\r\n                                </form>\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n          </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n\r\n\r\n<script>\r\n    export default {\r\n        data(){\r\n            return {\r\n                 form: new Form({\r\n                    id:'',\r\n                    name : '',\r\n                    email: '',\r\n                    password: '',\r\n                    type: '',\r\n                    bio: '',\r\n                    photo: ''\r\n                })\r\n            }\r\n        },\r\n        methods:{\r\n            getProfilePhoto(){\r\n                let photo = (this.form.photo.length > 200) ? this.form.photo : \"image/profile/\"+ this.form.photo ;\r\n                return photo;\r\n\r\n\r\n            },\r\n            updateInfo(){\r\n\r\n                this.$Progress.start();\r\n                if(this.form.password == ''){\r\n                    this.form.password = undefined;\r\n                }\r\n                this.form.put('api/profile')\r\n                .then(({data})=>{\r\n\r\n                    Fire.$emit('AfterCreate');\r\n                    this.$Progress.finish();\r\n                })\r\n                .catch(() => {\r\n                    this.$Progress.fail();\r\n                });\r\n            },\r\n            updateProfile(e){\r\n                let file = e.target.files[0];\r\n                let reader = new FileReader();\r\n                let limit = 1024 * 1024 * 2;\r\n\r\n                if(file['size'] > limit){\r\n                    swal.fire({\r\n                        type: 'error',\r\n                        title: 'Oops...',\r\n                        text: 'You are uploading a large file',\r\n                    })\r\n                    return false;\r\n                }\r\n                reader.onloadend = (file) => {\r\n                    this.form.photo = reader.result;\r\n                }\r\n                reader.readAsDataURL(file);\r\n            }\r\n        },\r\n        created() {\r\n            axios.get(\"api/profile\")\r\n            .then(({ data }) => (\r\n                this.form.fill(data))\r\n            );\r\n        }\r\n    }\r\n</script>\r\n"],"sourceRoot":""}]);
